<template>
  <div class="main_content">
    <Index-header></Index-header>
    <el-container>
      <div class="my_account">
        <div class="account_bg">
          <div class="account_bg_img">
            <div class="account_money">
              <label>账户余额</label>
              <p><span>{{total_cash}}</span> 积分</p>
            </div>
            <!-- <div class="fuwu">佣金收益：￥{{total_cash}}</div> -->
          </div>
        </div>
        <div class="menulist">
          <!-- <router-link :to="{name:'recharge'}">
            <div class="menulist_info">
              <div class="menulist_info_left">
                <img src="../../assets/images/mine/account_1.png" class="menulist_info_img"/>
                <label>充值</label>
              </div>
              <img src="../../assets/images/mine/back_icon.png" class="arrow_right" />
            </div>
          </router-link> -->
          <router-link :to="{name:'withdraw'}">
            <div class="menulist_info">
              <div class="menulist_info_left">
                <img src="../../assets/images/mine/account_2.png" class="menulist_info_img"/>
                <label>提现</label>
              </div>
              <img src="../../assets/images/mine/back_icon.png" class="arrow_right" />
            </div>
          </router-link>
          <router-link :to="{name:'menulist'}">
            <div class="menulist_info">
              <div class="menulist_info_left">
                <img src="../../assets/images/mine/account_3.png" class="menulist_info_img"/>
                <label>账单</label>
              </div>
              <img src="../../assets/images/mine/back_icon.png" class="arrow_right" />
            </div>
          </router-link>
        </div>
      </div>
    </el-container>
  </div>
</template>

<script>
import Header from "../common/Header";
import axios from "axios";
import Vue from "vue";
import commonURL from "../../assets/js/ajax";
import { UserWalletInfo } from "@/api/public.js";
import $ from "../../assets/js/jquery-3.3.1.min";
import wx from "weixin-js-sdk";
import moment from "moment";

export default {
  name: "myAccount",
  data() {
    return {
      total_cash: "",
      allow_cash: "",
      total_commission: "",
      loginData: "", //登陆的数据
    };
  },
  components: {
    "Index-header": Header
  },
  created() {
    this.$store.state.title = "钱包";
    this.$store.state.linkName = "mine";
  },
  mounted() {
    if(!!localStorage.getItem("loginData")){
      this.loginData = JSON.parse(localStorage.getItem("loginData"));
    }
    document.querySelector('html').setAttribute('style', 'background-color:#fff')

    let params = {
      uid: this.loginData.id,
      sid: this.loginData.sid
    };
    UserWalletInfo(params).then(res => {
      if (res.data.code === 0) {
        this.total_cash = res.data.data.balance/100;
      } else {
        this.$message.error(res.data.msg);
      }
    });
  },
  methods: {

  },
  beforeDestroy () {
    document.querySelector('html').removeAttribute('style')
  }
};
</script>

<style scoped>
.main_content {
  width: 100%;
  height: 100%;
  background: #fff;
}
h4 {
  text-align: center;
  height: 44px !important;
  line-height: 44px;
  padding: 0 10px;
  color: #333;
  background: #fff;
  border-bottom: 1px solid #e3e3e3;
}
h4 a {
  float: left;
  color: #333;
  font-size: 0.8rem;
}
.my_account{
  display: flex;
  flex-direction: column;
  width: 100%;
}
.account_bg{
  width: 100%;
  height: 190px;
  color: #fff;
}
.account_bg_img{
  height: 160px;
  margin: 15px 20px;
  background: url(../../assets/images/mine/account_bg.png) no-repeat;
  background-size: 100% 100%;
}
.fuwu{
  padding-top: 25px;
  margin-left: 27px;
  text-align: left;
  font-size: 12px;
}
.account_money{
  padding-top: 25px;
  margin-left: 27px;
  text-align: left;
}
.account_money label{
  font-size: 12px;

}
.account_money p{
  margin-top: 20px;
}
.account_money p span{
  font-size: 38px;
}
.account_withdraw{
  margin-right: 30px;
  text-align: right;
  font-size: 13px;
}
.account_withdraw img{
  margin-left: 5px;
  width: 6px;
  height: 10px;
}

.menulist {
  width: 100%;
  padding: 0px 20px;
}
.menulist_info {
  display: flex;
  justify-content: space-between;
  width: 100%;
  height: 48px;
  line-height: 48px;
  color: #333;
  font-size: 13px;
  box-sizing: border-box;
}
.menulist_info_left{
  display: flex;
}
.menulist_info_img{
  display: flex;
  margin-top: 16px;
  margin-right: 13px;
  width: 16px;
  height: 16px;
}
.arrow_right{
  display: flex;
  width: 6px;
  height: 11px;
  margin-top: 16px;
}
a {
  color: #ccc;
}
</style>



