import Vue from 'vue';
import Router from 'vue-router';
import login from '@/components/page/login';   //密码登录
import register from '@/components/page/register';   //注册
import home from '@/components/page/home';   //首页
import mine from '@/components/page/mine';   //我的
import authentically from '@/components/page/authentically'; //认证
import share from '@/components/page/share'; //邀请好友
import myAccount from '@/components/page/myAccount'; //我的账户
import menulist from '@/components/page/menulist'; //账单明细
import cardlist from '@/components/page/cardlist'; //银行卡
import cardadd from '@/components/page/cardadd'; //添加银行卡
import carddetail from '@/components/page/carddetail'; //银行卡详情
import withdraw from '@/components/page/withdraw'; //提现
import parentslist from '@/components/page/parentslist'; //好友列表
import parentslistdetail from '@/components/page/parentslistdetail'; //好友列表详情
import recharge from '@/components/page/recharge'; //充值
import updateLevel from '@/components/page/updateLevel'; //升级
import updateList from '@/components/page/updateList'; //升级记录

Vue.use(Router);

export default new Router({
  // mode:'history',
  routes: [
    {
      path: '/login',
      name: 'login',
      component: login
    },{
      path: '/register',
      name: 'register',
      component: register
    },{
      path: '/',
      name: 'mine',
      component: mine
    },{
      path: '/home',
      name: 'home',
      component: home
    },{
      path: '/mine',
      name: 'mine',
      component: mine
    },{
      path: '/authentically',
      name: 'authentically',
      component: authentically
    },{
      path: '/share',
      name: 'share',
      component: share
    },{
      path: '/parentslistdetail',
      name: 'parentslistdetail',
      component: parentslistdetail
    },{
      path: '/parentslist',
      name: 'parentslist',
      component: parentslist
    },
    {
      path: '/myAccount',
      name: 'myAccount',
      component: myAccount
    },
    {
      path: '/menulist',
      name: 'menulist',
      component: menulist
    },
    {
      path: '/cardlist',
      name: 'cardlist',
      component: cardlist
    },
    {
      path: '/cardadd',
      name: 'cardadd',
      component: cardadd
    },
    {
      path: '/carddetail',
      name: 'carddetail',
      component: carddetail
    },
    {
      path: '/withdraw',
      name: 'withdraw',
      component: withdraw
    },
    {
      path: '/recharge',
      name: 'recharge',
      component: recharge
    },
    {
      path: '/updateLevel',
      name: 'updateLevel',
      component: updateLevel
    },
    {
      path: '/updateList',
      name: 'updateList',
      component: updateList
    },
  ]
});




