<template>
    <div>
        <el-header class="title-text">
            <div>
                <router-link :to="{name:linkName,query:linkQuery}"><i class="el-icon-arrow-left"></i></router-link>
                <label>{{title}}</label>
            </div>
        </el-header>
    </div>

</template>
<script>
    export default{
        name:'Header',
        data(){
          return{
              title:'',
              linkName:'',
              linkQuery:'',
          }
        },
        mounted(){
            this.title = this.$store.state.title;
            this.linkName = this.$store.state.linkName;
            this.linkQuery = this.$store.state.linkQuery;
        }
    }
</script>
<style scoped>
    .el-header {
        position: relative;
        text-align: center;
        height: 44px !important;
        line-height: 44px;
        padding: 0 10px;
        /* border-bottom: 1px solid #E3E3E3; */
        z-index: 100;
    }
    .title-text{
        background: #fff;
    }
    .title-text label{
        text-align: center;
        color: #333;
    }
    a{
        position: absolute;
        left: 10px;
        text-align: left;
        color: #333;
    }
</style>




