<template>
  <div class="main_content">
    <Index-header></Index-header>
    <el-container>
      <div class="my_account">
        <router-link :to="{name:'cardlist',query:{backtype:'withdraw'}}">
          <div class="menulist_info">
            <div class="menulist_info_left">
              <label>到账银行卡</label>
              <span v-if="nocard">请添加银行卡</span>
              <span class="sel_bank" v-else>
                <div class="sel_bank_div">{{getCard.opening_bank}}</div>
                <div class="sel_bank_div2">尾号{{getCard.card_number}}储蓄卡</div>
              </span>
            </div>
            <img src="../../assets/images/mine/back_icon.png" class="arrow-right" />
          </div>
        </router-link>
        <div class="login_ul">
          <div class="login_info">
            <label>提现金额</label>
          </div>
          <div class="login_info" style="border-bottom:1px solid #e3e3e3">
            <img src="../../assets/images/mine/price.png" class="price_icon" />
            <input type="number" v-model="money" placeholder="请输入金额">
          </div>
          <div class="login_info">
            <p>当前可提现余额 {{allow_cash}}, <span @click="allSure">全部提现</span></p>
          </div>
        </div>
        <div class="bottom_btn">
          <div class="login_btn">
            <button @click="sure">确定</button>
          </div>
        </div>
      </div>
    </el-container>
    <div class="mask" @click="close"></div>
    <div id="logout" class="dialog">
      <div class="dialog-content"></div>
      <div class="dialog-btn clearfix">
        <a class="dialog_sure2" @click="close">确认</a>
      </div>
    </div>
  </div>
</template>

<script>
import Header from "../common/Header";
import axios from "axios";
import Vue from "vue";
import commonURL from "../../assets/js/ajax";
import { UserWalletInfo , applyWithdraw , UserBankCardList } from "@/api/public.js";
import $ from "../../assets/js/jquery-3.3.1.min";
import wx from "weixin-js-sdk";
import moment from "moment";

export default {
  name: "withdraw",
  data() {
    return {
      loginData: {}, //登陆的数据
      haveTap: 0, // 屏蔽重复点击
      allow_cash:'',
      money: "",
      cardcom: "",
      card: "",
      nocard: true,
      selData:'',
      cardOne:{},
      getCard:{
        title:'',
        card_number:'',
      },
    };
  },
  components: {
    "Index-header": Header
  },
  created() {
    if(this.$route.query.selData){
      this.selData = this.$route.query.selData;
    }
    this.$store.state.title = "提现";
    this.$store.state.linkName = "myAccount";

    if(!!localStorage.getItem("loginData")){
      this.loginData = JSON.parse(localStorage.getItem("loginData"));
    }

    if(!!this.selData){
      this.getCard = this.selData;
      this.getCard.card_number = this.getCard.card_no.slice(this.getCard.card_no.length-4,this.getCard.card_no.length);
      this.nocard = false;
    }else{
      let params = {
        uid: this.loginData.id,
        sid: this.loginData.sid,
        page_index: 1,
        page_size: 10
      };
      UserBankCardList(params).then(res => {
        if (res.data.code === 0) {
          if(res.data.data.length>0){
            var getdata = res.data.data;
            if(getdata.length>0){
              this.cardOne = res.data.data[0];
              this.getCard = this.cardOne;
              this.getCard.card_number = res.data.data[0].card_no.slice(0,4);
              this.nocard = false;
            }
          }
        } else {
          this.$message.error(res.data.data.msg);
        }
      });
    }
  },
  mounted() {
    document.querySelector('html').setAttribute('style', 'background-color:#F5F5F5')

    let params = {
      uid: this.loginData.id,
      sid: this.loginData.sid
    };
    UserWalletInfo(params).then(res => {
      if (res.data.code === 0) {
        this.total_cash = res.data.data.balance/100;
        this.allow_cash = res.data.data.balance/100;
      } else {
        this.$message.error(res.data.msg);
      }
    });

  },
  methods: {
    allSure(){
      if(this.allow_cash>0){
        this.money = this.allow_cash;
      }else{
        $(".mask").show();
        $("#logout").show();
        $(".dialog-content").text('可提现余额不足！');
      }
    },
    close() {
      $(".mask").hide();
      $("#logout").hide();
    },
    sure() {
      if(this.nocard == true){
        $(".mask").show();
        $("#logout").show();
        $(".dialog-content").text('请选择银行卡！');
        return false;
      }
      if (this.money == "") {
        $(".mask").show();
        $("#logout").show();
        $(".dialog-content").text('请输入提现金额！');
        return false;
      }

      if(this.allow_cash>0){
        this.money = this.allow_cash;
      }else{
        $(".mask").show();
        $("#logout").show();
        $(".dialog-content").text('可提现余额不足！');
      }

      if (this.money > this.allow_cash) {
        $(".mask").show();
        $("#logout").show();
        $(".dialog-content").text('可提现余额不足！');
        return false;
      }

      const that = this;
      if (this.haveTap === 0) {
        that.haveTap = 1;
        let params = {
          amount: this.money,
          card_no: this.getCard.card_no,
          bank_name: this.getCard.opening_bank,
          branch_name: this.getCard.branch_bank,
          name: this.getCard.name,
          uid: this.loginData.id,
          sid: this.loginData.sid
        };
        applyWithdraw(params).then(res => {
          that.haveTap = 0;
          if (res.data.code === 0) {
            that.$message.success(res.data.msg);
            this.$router.push({ name: "myAccount" });
          } else {
            that.$message.error(res.data.msg);
          }
        });
      } else {
        console.log("屏蔽重复点击");
        that.$message.error("点击频率过快");
      }
    }
  }
};
</script>

<style scoped>
.main_content {
  width: 100%;
  height: 100%;
}
h4 {
  position: relative;
  text-align: center;
  height: 44px !important;
  line-height: 44px;
  padding: 0 10px;
  color: #333;
  background: #fff;
  border-bottom: 1px solid #e3e3e3;
}
h4 a {
  position: absolute;
  right: 10px;
  color: #333;
  font-size: 12px;
  font-weight: 400;
}
.my_account{
  display: flex;
  flex-direction: column;
  width: 100%;
  margin-top: 5px;
}
.menulist_info {
  display: flex;
  justify-content: space-between;
  width: 100%;
  height: 60px;
  line-height: 60px;
  color: #333;
  font-size: 13px;
  box-sizing: border-box;
  padding: 0px 10px;
  background: #fff;
}
.menulist_info_left{
  display: flex;
}
.menulist_info_left span{
  margin-left: 20px;
}
.menulist_info_img{
  display: flex;
  margin-top: 15px;
  margin-right: 11px;
  width: 16px;
  height: 18px;
}
.menulist_info_img2{
  display: flex;
  margin-top: 15px;
  margin-right: 9px;
  width: 21px;
  height: 18px;
}
.arrow-right{
  display: flex;
  width: 8px;
  height: 14px;
  margin-top: 16px;
}
.price_icon{
  display: flex;
  width: 15px;
  height: 19px;
  margin-top: 15px;
}
.login_ul {
  width: 100%;
  background: #fff;
  margin-top: 8px;
}
.login_info {
  color: #333;
  font-size: 13px;
  box-sizing: border-box;
  display: flex;
  height: 45px;
  line-height: 45px;
  margin: 0px 10px;
}
.login_info label {
  color: #999;
  width: 20%;
  text-align: left;
}
.login_info input {
  width: 70%;
  margin-left: 3%;
  border: none;
  padding: 0px 5px;
  display: flex;
  align-items: center;
  font-size: 20px;
}
.login_info span{
  margin-left: 3px;
  color: #FB8B40;
  cursor: pointer;
}
.sel_bank{
  display: flex;
  flex-direction: column;
  text-align: left;
}
.sel_bank_div{
  height: 30px;
  line-height: 38px;
  font-size: 14px;
}
.sel_bank_div2{
  height: 30px;
  line-height: 22px;
  color: #666;
  font-size: 12px;
}
.tishi{
  font-size: 12px;
  text-align: left;
  padding: 10px;
}
.bottom_btn{
  width: 100%;
  margin: 20px auto;
}
.login_btn {
  width: 96%;
  margin: auto;
  text-align: center;
}
.login_btn button {
  width: 100%;
  border: none;
  padding: 10px;
  border-radius: 4px;
  color: #fff;
  font-size: 1rem;
  background: #383838;
}
input::-webkit-input-placeholder{
  color: #999999;
  font-size: 14px;
}
</style>



