<template>
  <div>
    <Index-header></Index-header>
    <div class="realtime_div">
      <div class="realtime_search clearfix">
        <div class="head_input_box">
          <img src="../../assets/images/search.png"/>
          <form @submit.prevent>
            <input v-on:input="search" ref="input" type="search" v-model="mobile" placeholder="输入手机号搜索"/>
          </form>
        </div>
        <div class="realtime_search_img" @click="search">搜索</div>
      </div>
      <div class="list_title">
        <label>账号</label>
        <label>已邀请人数</label>
        <label class="right_text">加入时间</label>
      </div>
      <div class="realtime_content" v-for="(item,index) in allProjects" :key="index">
        <!-- <router-link :to="{name:'parentslistdetail',query:{child_uid:item.uid}}"> -->
          <div class="list_content" @click="goCild(item.uid,item.invite_count)">
            <label>{{item.mobile}}
              <img src="../../assets/images/mine/v1.png" v-if="item.vip_level=='1'"/>
              <img src="../../assets/images/mine/v2.png" v-if="item.vip_level=='2'"/>
              <img src="../../assets/images/mine/v3.png" v-if="item.vip_level=='3'"/>
              <img src="../../assets/images/mine/v4.png" v-if="item.vip_level=='4'"/>
              <img src="../../assets/images/mine/v5.png" v-if="item.vip_level=='5'"/>
              <img src="../../assets/images/mine/v6.png" v-if="item.vip_level=='6'"/>
              <img src="../../assets/images/mine/v7.png" v-if="item.vip_level=='7'"/>
              <img src="../../assets/images/mine/v8.png" v-if="item.vip_level=='8'"/>
              <img src="../../assets/images/mine/v9.png" v-if="item.vip_level=='9'"/>
            </label>
            <label class="">{{item.invite_count}}</label>
            <span class="right_text list_time">{{item.create_time}}</span>
          </div>
        <!-- </router-link> -->
      </div>
    </div>
    <div>
      <div class="nodata" v-if="ismore">上拉加载更多</div>
      <div class="nodata" v-else>无更多数据</div>
    </div>
  </div>
</template>

<script>
import Header from "../common/Header";
import axios from "axios";
import Vue from "vue";
import commonURL from "../../assets/js/ajax";
import { parentsList } from "@/api/public.js";
import $ from "../../assets/js/jquery-3.3.1.min";
import wx from "weixin-js-sdk";
import moment from "moment";
import PullTo from "vue-pull-to";

export default {
  name: "parentslistdetail",
  data() {
    return {
      mobile:'',
      loginData: {}, //登陆的数据
      child_uid:'',
      havedata:true,
      allProjects:[],
      page:1,
      iconLink: '',
      ismore:false
    };
  },
  components: {
    "Index-header": Header,
    PullTo
  },
  created() {
    this.$store.state.title = "下级好友列表";
    this.$store.state.linkName = "parentslist";
    this.child_uid = this.$route.query.child_uid;
    this.$store.state.linkQuery = {child_uid:this.child_uid};
  },
  mounted() {
    document.querySelector('html').setAttribute('style', 'background-color:#F5F5F5')

    if(!!localStorage.getItem("loginData")){
      this.loginData = JSON.parse(localStorage.getItem("loginData"));
    }
    this.getAllData(this.child_uid);
    var that = this;
    $(window).scroll(function(){
        var scrollTop = $(this).scrollTop();    //滚动条距离顶部的高度
        var scrollHeight = $(document).height();   //当前页面的总高度
        var clientHeight = $(this).height();    //当前可视的页面高度
        if(scrollTop + clientHeight == scrollHeight){
            //距离顶部+当前高度 >=文档总高度 即代表滑动到底部 注：-50 上拉加载更灵敏
            //加载数据
            if(that.ismore == true){
                that.getAllData(this.child_uid);
            }
        }
    });
  },
  methods: {
    // loadmore(loaded) {
    //     this.pageNum++;
    //     setTimeout(() => {
    //         this.getAllData();
    //         loaded('done');
    //     }, 2000);
    // },
    getAllData(uid){
      let params = {
        mobile: '',
        child_uid: uid,
        uid: this.loginData.id,
        sid: this.loginData.sid,
        page_index: this.page,
        page_size: 10
      };
      parentsList(params).then(res => {
        if (res.data.code === 0) {
          var getdata = res.data.data.list;
          if(getdata.length>0){
              for (var i = 0; i < getdata.length; i++) {
                var newDate = new Date();
                newDate.setTime(getdata[i].create_time * 1000);
                getdata[i].create_time = this.$moment(newDate).format(
                "YYYY-MM-DD HH:mm:ss"
                );
                this.allProjects.push(getdata[i]);
            }
            if (getdata.length < 10) {
                this.ismore = false;
            } else {
                this.page++;
                this.ismore = true;
            }
          }else{
              this.ismore = false;
          }

        } else {
          this.$message.error(res.data.msg);
        }
      });
    },
    search(){
      this.page = 1;
      this.allProjects = [];
      this.getAllData(this.child_uid);
    },
    goCild(uid,count){
      if(count != 0){
        this.page = 1;
        this.allProjects = [];
        this.child_uid = uid;
        this.getAllData(uid);
      }
    }
  },
  beforeDestroy () {
    document.querySelector('html').removeAttribute('style')
  }
};
</script>

<style scoped >

.realtime_div{
  margin-top: 5px;
  background: #fff;
}
.head_input_box {
  position: relative;
  width: 86%;
}
.head_input_box img {
  width: 13px;
  height: 13px;
  position: absolute;
  left: 15px;
  top: 13px;
}
.head_input_box input {
  background: #fff;
  border-radius: 20px;
  border: 1px solid #303030;
  height: 39px;
  display: flex;
  align-items: center;
  color: #999999;
  font-size: 13px;
  width: 100%;
  padding-left: 35px;
}
.head_input_box input::-webkit-input-placeholder {
  color: #999999;
  font-size: 13px;
}
.realtime_search{
  width: 100%;
  height: 59px;
  line-height: 59px;
  padding: 10px 3%;
  display: flex;
  justify-content: space-between;
}
.realtime_search_img{
  height: 40px;
  line-height: 40px;
  font-size: 14px;
}

.list_title{
  display: flex;
  justify-content: space-between;
  background: #E9E9E9;
  height: 40px;
  line-height: 40px;
  padding: 0px 3%;
  color: #333;
  font-size: 14px;
}
.right_text{
  padding-right: 30px;
}
.realtime_content{
  background: #fff;
  width: 100%;
}

.list_content{
  display: flex;
  justify-content: space-between;
  height: 50px;
  line-height: 50px;
  padding: 0px 3%;
  color: #333;
  font-size: 14px;
}
.list_content label{
  display: flex;
}
.list_content img{
  width: 24px;
  height: 15px;
  margin-left: 5px;
  margin-top: 15px;
}
.list_time{
  padding-right: 20px;
  color: #666;
  font-size: 12px;
}
.nodata{
  font-size: 13px;
}
</style>



