<template>
    <div>
        <el-header class="title-text">
            <div>
                <router-link :to="{name:linkName,query:linkQuery}"><i class="el-icon-arrow-left"></i></router-link>
                <label>{{title}}</label>
                <span @click="showRule">记录</span>
            </div>
        </el-header>
    </div>

</template>
<script>
    export default{
        name:'Header3',
        data(){
          return{
              title:'',
              linkName:'',
              linkQuery:'',
              isdyr:''
          }
        },
        mounted(){
            this.title = this.$store.state.title;
            this.linkName = this.$store.state.linkName;
            this.linkQuery = this.$store.state.linkQuery;
        },
        methods:{
            showRule(){
                this.$router.push({name:'updateList'});
            }
        }
    }
</script>
<style scoped>
    .el-header {
        position: relative;
        text-align: center;
        height: 44px !important;
        line-height: 44px;
        padding: 0 10px;
        z-index: 100;
    }
    .title-text{
      background: #fff;
      /* background: -moz-linear-gradient(left, #0D7CFA 0%, #30ACFC 100%);
      background: -webkit-gradient(
        linear,
        top left,
        top right,
        color-stop(0%, #0D7CFA),
        color-stop(100%, #30ACFC)
      );
      background: -webkit-linear-gradient(left, #0D7CFA 0%, #30ACFC 100%);
      background: -o-linear-gradient(left, #0D7CFA 0%, #30ACFC 100%);
      background: -ms-linear-gradient(left, #0D7CFA 0%, #30ACFC 100%);
      background: linear-gradient(to right, #0D7CFA 0%, #30ACFC 100%); */
    }
    .title-text label{
        text-align: center;
        color: #333;
    }
    a{
        position: absolute;
        left: 10px;
        text-align: left;
        color: #333;
    }
    span{
        position: absolute;
        right: 10px;
        color: #333;
        font-size: 12px;
    }
</style>




