      }
    };
  },
  components: {
    "Index-header": Header
  },
  created() {
    if(this.$route.query.selData){
      this.selData = this.$route.query.selData;
    }
    this.$store.state.title = "充值";
    this.$store.state.linkName = "myAccount";

    if(!!localStorage.getItem("loginData")){
      this.loginData = JSON.parse(localStorage.getItem("loginData"));
    }

  },
  mounted() {
    document.querySelector('html').setAttribute('style', 'background-color:#F5F5F5')
  },
