  mounted() {

    document.querySelector('body').removeAttribute('style')
    if(!!localStorage.getItem("loginData")){
      this.loginData = JSON.parse(localStorage.getItem("loginData"));
      //更新用户数据
      let params = {
        uid: this.loginData.id,
        sid: this.loginData.sid
      };
      refreshUserInfo(params).then(resp => {
        if (resp.data.code === 0) {
        } else if(resp.data.code === 1111){
          this.$router.push({name:'login'})
        }else{
          this.$message.error(resp.data.msg);
        }
      });
      //支付成功更新用户数据
      updateProfitGraph(params).then(resp => {
        if (resp.data.code === 0) {
          var olddata = JSON.parse(localStorage.getItem("loginData"));
          olddata.vip_level = resp.data.data.vip_level;
          localStorage.setItem('loginData',JSON.stringify(olddata));
          this.loginData = JSON.parse(localStorage.getItem("loginData"));
          console.log(this.loginData)
        } else {
          this.$message.error(resp.data.msg);
        }
      });
    }else{
      this.$router.push({name:'login'})
    }
    console.log('cookie:',$cookies.get("status"))
  },
  methods: {
    //安全退出
    logout() {
      $(".mask").show();
      $("#logout").show();
    },
    close() {
      $(".mask").hide();
      $("#logout").hide();
    },
    sure() {
      let params = {
        uid: this.loginData.id,
        sid: this.loginData.sid
      };
      updateUserLogoutmobile(params).then(res => {
        if (res.data.code === 0) {
          localStorage.setItem('loginData','');
          this.$router.push({ name: "login" });
        } else {
          this.$message.error(res.data.msg);
        }
      });
    },
    close2() {
      $(".mask").hide();
      $("#logout").hide();
    }
  }
};
