<template>
    <div>
      <el-header class="title-text">
        <div>
          <router-link :to="{name:linkName,query:linkQuery}"><i class="el-icon-arrow-left"></i></router-link>
          <label>{{title}}</label>
          <span @click="Delete">解绑</span>
        </div>

        <div class="mask" @click="close"></div>
        <div id="logout" class="dialog">
          <div class="dialog-content">是否确定解绑？</div>
          <div class="dialog-btn clearfix">
            <div class="dialog_cancel" @click="close">取消</div>
            <div class="dialog_sure" @click="sureDelete">确认</div>
          </div>
        </div>
      </el-header>
    </div>

</template>
<script>
import Header from "../common/Header2";
import axios from "axios";
import Vue from "vue";
import commonURL from "../../assets/js/ajax";
import { delUserBankCard } from "@/api/public.js";
import $ from "../../assets/js/jquery-3.3.1.min";
    export default{
        name:'Header2',
        data(){
          return{
              title:'',
              linkName:'',
              linkQuery:'',
              cardbacktype:'',
              haveTap: 0, // 屏蔽重复点击
              id:''
          }
        },
        mounted(){
            if(!!localStorage.getItem("loginData")){
              this.loginData = JSON.parse(localStorage.getItem("loginData"));
            }
            this.title = this.$store.state.title;
            this.linkName = this.$store.state.linkName;
            this.linkQuery = this.$store.state.linkQuery;
            if(this.$route.query.backtype){
              this.backtype = this.$route.query.backtype;
              this.cardbacktype = this.backtype;
            }
            if(this.$route.query.carddata){
              this.id = this.$route.query.carddata.id;
            }
        },
        methods:{
          close() {
            $(".mask").hide();
            $("#logout").hide();
          },
          Delete() {
            $(".mask").show();
            $("#logout").show();
          },
          sureDelete() {
            $(".mask").hide();
            $("#logout").hide();
            const that = this;
            if (this.haveTap === 0) {
              that.haveTap = 1;
              let params = {
                id: this.id,
                uid: this.loginData.id,
                sid: this.loginData.sid
              };
              delUserBankCard(params).then(res => {
                that.haveTap = 0;
                if (res.data.code === 0) {
                  that.$router.push({ name: "cardlist" ,query:{backtype:this.backtype}});
                } else {
                  that.$message.error(res.data.msg);
                }
              });
            } else {
              console.log("屏蔽重复点击");
              that.$message.error("点击频率过快");
            }
          }
        }
    }
</script>
<style scoped>
    .el-header {
        position: relative;
        text-align: center;
        height: 44px !important;
        line-height: 44px;
        padding: 0 10px;
        /* border-bottom: 1px solid #E3E3E3; */
        z-index: 100;
    }
    .title-text{
        background: #fff;
    }
    .title-text label{
        text-align: center;
        color: #333;
    }
    .title-text span{
        position: absolute;
        right: 10px;
        height: 22px;
        line-height: 22px;
        margin-top: 10px;
        font-size: 12px;
        border: 1px solid #333;
        padding: 0px 12px;
        text-align: center;
        color: #333;
    }
    a{
        position: absolute;
        left: 10px;
        text-align: left;
        color: #333;
    }
    .dialog-btn{
      line-height: 1 !important;
    }
</style>




