<template>
  <div class="main_content">
    <el-container>
      <div class="login_bg">
        <div class="login_bg_img">
          <img src="../../assets/images/login_bg.png">
        </div>
        <div class="login_bg_title">注册</div>
        <div class="login_ul">
          <div class="login_info">
            <label>手机号码</label>
            <input type="number" v-model="mobile" placeholder="请输入手机号">
          </div>
          <div class="login_info">
            <label>验证码</label>
            <div class="login_code">
              <input id="verify" class="loan_inut" placeholder="请输入验证码">
              <label id="getVerify">
                <span>
                  <a @click="makeIdentifyCode">获取验证码 </a><i></i>
                </span>
              </label>
            </div>
          </div>
          <div class="login_info">
            <label>推荐码</label>
            <span class="yqcode" v-if="isGet">{{idcode}}</span>
            <input class="yqcode" type="text" v-model="idcode" placeholder="请输入推荐码" v-else>
          </div>
          <p class="Ehint"></p>
          <div class="login_btn">
            <button @click="register">注册</button>
          </div>
        </div>
      </div>
    </el-container>
    <div class="mask" @click="close"></div>
    <div id="logout" class="dialog">
      <div class="dialog-content"></div>
      <div class="dialog-btn clearfix">
        <a class="dialog_sure2" @click="close">确认</a>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios';
import Vue from 'vue';
import commonURL from "../../assets/js/ajax";
import utils from "../../assets/js/utils";
import { get_code , sign_up } from "@/api/public.js";
import $ from "../../assets/js/jquery-3.3.1.min";
import wx from 'weixin-js-sdk';
import moment from "moment";

export default {
  name: 'register',
  data(){
    return{
      name:'',
      mobile: "",
      idcode: "",
      surepassword: "",
      yzcode: '',
      InterValObj: '',  //timer变量，控制时间
      curCount: 60,  //当前剩余秒数
      isGet: true,
      title:'',
      haveTap: 0, // 屏蔽重复点击
      codeType: 1, //  1：注册，5：登录
    }
  },
  components:{
  },
  created(){
  },
  mounted() {
    this.idcode = utils.getUrlKey('idcode');
    if(!!this.idcode){
      this.isGet = true;
    }else{
      this.isGet = false;
    }
  },
  methods:{
    close() {
      $(".mask").hide();
      $("#logout").hide();
    },
    makeIdentifyCode(){
      const that = this;
      if (this.haveTap === 0) {
        var re = /^\d{11}$/;
        if(re.test(this.mobile)){
          that.haveTap = 1;
          let params = {
            accepter: that.mobile,
            code_type: that.codeType
          };
          $(".Ehint").hide();
          $("#getVerify a").hide();
          $("#getVerify i").text("获取验证码");
          //获取验证码
          get_code(params).then(res => {
            // console.log(res);
            that.haveTap = 0;
            if (res.data.code === 0) {
              this.curCount = 60;
              $("#getVerify i").text(this.curCount + "s");
              that.InterValObj = window.setInterval(function(){
                if (that.curCount == 1) {
                  window.clearInterval(that.InterValObj);
                  $("#getVerify a").show();
                  $("#getVerify a").text("重新发送");
                  $("#getVerify i").empty();
                } else {
                  that.curCount = Number(that.curCount)-1;
                  $("#getVerify i").text(that.curCount + "s");
                }
              }, 1000);
              that.$alert(res.data.msg, '提示', {
                confirmButtonText: '确定',
                callback: action => {
                }
              });
            }else{
              $("#getVerify a").show().text("重新发送");
              $("#getVerify i").empty();
              $(".Ehint").text("获取验证码失败，请重试！").show();
              setTimeout(function(){
                  $(".Ehint").hide();
              },3000);
              that.$alert(res.data.msg, '提示', {
                confirmButtonText: '确定',
                callback: action => {
                }
              });
            }
          });
        }else{
          $(".Ehint").text("提示：请输入正确的手机号码！").show();
          setTimeout(function(){
              $(".Ehint").hide();
          },3000);
        }
      } else {
        console.log("屏蔽重复点击");
        that.$message.error("点击频率过快");
      }
    },
    register() {
      const that = this;
      if (this.haveTap === 0) {

        if (this.mobile == "" || $("#verify").val() == "") {
          $(".mask").show();
          $("#logout").show();
          $(".dialog-content").text('请输入完整信息！');
        } else {
          // var Regx = /^[1][3,4,5,7,8,9][0-9]{9}$/;
          var Regx = /^\d{11}$/;
          if(!Regx.test(this.mobile)){
            $(".mask").show();
            $("#logout").show();
            $(".dialog-content").text('请输入正确的手机号！');
            return false;
          }

          that.haveTap = 1;
          let params = {
            mobile: that.mobile,
            code: $("#verify").val(),
            idcode: that.idcode,
            password: '',
          };
          sign_up(params).then(res => {
            that.haveTap = 0;
            if (res.data.code === 0) {
              this.$router.push({ name: "login" });
            } else {
              that.$message.error(res.data.msg);
            }
          });
        }
      }else {
        console.log("屏蔽重复点击");
        that.$message.error("点击频率过快");
      }
    },
  },
  destroyed(){
    if(this.InterValObj) { //如果定时器在运行则关闭
      clearInterval(this.InterValObj);
    }
  }
}

</script>

<style scoped>
.main_content {
  width: 100%;
  height: 100%;
  background: #fff;
}
.login_bg{
  width: 100%;
  display: flex;
  flex-direction: column;
}
.login_bg_img{
  display: flex;
  width: 100%;
  justify-content: flex-end;
}
.login_bg_img img{
  display: block;
  width: 117px;
  height: 117px;
}
.login_bg_title{
  text-align: left;
  padding-left: 32px;
  margin-bottom: 20px;
  font-size: 32px;
  color: #000;
}
.login_ul {
  width: 100%;
  padding: 0px 32px;
}
.login_info {
  width: 100%;
  color: #333;
  font-size: 17px;
  border-bottom: 1px solid #F5F5F5;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
}
.login_info label {
  padding-top: 20px;
  padding-bottom: 5px;
  text-align: left;
  color: #1A1A1A;
}
.login_info input {
  width: 100%;
  border: none;
  padding: 10px 0px;
  display: flex;
  align-items: center;
  font-size: 14px;
}
.login_code{
  display: flex;
  justify-content: space-between;
}
.lastinfo{
  padding: 25px 0px;
  border-bottom: none !important;
}
.login_info p {
  width: 100%;
  text-align: right;
  font-size: 12px;
}
.login_info p a{
  color: red;
}
.login_btn {
  margin-top: 50px;
  margin-bottom: 20px;
  text-align: center;
}
.login_btn button {
  width: 100%;
  border: none;
  padding: 10px;
  border-radius: 4px;
  color: #fff;
  font-size: 1rem;
  background: #383838;
}
#verify{
  width: 70%;
}
#getVerify{
  height: 4px;
  line-height: 4px;
  padding: 10px 0px;
  margin-top: 5px;
}
input::-webkit-input-placeholder{
  color: #999999;
  font-size: 14px;
}
.yqcode{
  width: 100%;
  padding: 10px 0px;
  display: flex;
  align-items: center;
  font-size: 14px;
  color: #333;
}
p{
  width: 50%;
  margin: auto;
  text-align: center;
  padding: 25px 0px;
  font-size: 12px;
}
.register_btn{
  float: left;
  width: 100%;
  text-align: center;
}
.register_btn button {
  width: 100%;
  border: 1px solid #383838;
  padding: 10px;
  border-radius: 10px;
  color: #383838;
  font-size: 1rem;
  background: #fff;
}
</style>



