<template>
  <div class="main_content">
    <Index-header></Index-header>
    <div class="card_all">
      <label>我的银行卡 <span>共{{cardList.length}}张</span></label>
      <img src="../../assets/images/mine/card_add.png" @click="cardadd" class="card_add"/>
    </div>
    <div class="card_nothing" v-if="cardList.length == 0">
      <div class="tips">
        <img src="../../assets/images/mine/card_no.png" />
        <p>暂无银行卡</p>
      </div>
    </div>
    <div class="card" v-else>
      <div class="card_box">
        <div class="card_box_item" v-for="(item,index) in cardList" :key="index">
          <router-link :to="{name:'carddetail',query:{carddata:item,backtype:backtype}}" v-if="ischeck==false">
            <div class="card_logo">
              <img src="../../assets/images/mine/card_sign.png" />
            </div>
            <div class="item_content">
              <div class="item_content_title">{{item.opening_bank}}</div>
              <div class="item_content_num">{{item.card_no}}</div>
            </div>
          </router-link>
          <router-link :to="{name:'withdraw',query:{selData:item}}" v-else>
            <div class="card_logo">
              <img src="../../assets/images/mine/card_sign.png" />
            </div>
            <div class="item_content">
              <div class="item_content_title">{{item.opening_bank}}</div>
              <div class="item_content_num">{{item.card_no}}</div>
            </div>
          </router-link>
        </div>
        <div>
          <div class="nodata" v-if="ismore">上拉加载更多</div>
          <div class="nodata" v-else>无更多数据</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Header from "../common/Header";
import axios from "axios";
import Vue from "vue";
import commonURL from "../../assets/js/ajax";
import { UserBankCardList } from "@/api/public.js";
import $ from "../../assets/js/jquery-3.3.1.min";
import wx from "weixin-js-sdk";
import moment from "moment";

export default {
  name: "cardlist",
  data() {
    return {
      loginData: {}, //登陆的数据
      page: 1,
      cardList:[],
      ismore:false,
      backtype:'',
      ischeck: false,
      cardbacktype:''
    };
  },
  components: {
    "Index-header": Header
  },
  created() {
    if(this.$route.query.backtype){
      this.backtype = this.$route.query.backtype;
      this.cardbacktype = this.backtype;
    }
    if(this.backtype == 'withdraw'){
      this.$store.state.linkName = "withdraw";
      this.ischeck = true;
    }else{
      this.$store.state.linkName = "mine";
    }
    this.$store.state.title = "银行卡";
  },
  mounted() {
    if(!!localStorage.getItem("loginData")){
      this.loginData = JSON.parse(localStorage.getItem("loginData"));
    }
    document.querySelector('html').setAttribute('style', 'background-color:#F5F5F5')
    this.getAllData();
    var that = this;
    $(window).scroll(function(){
        var scrollTop = $(this).scrollTop();    //滚动条距离顶部的高度
        var scrollHeight = $(document).height();   //当前页面的总高度
        var clientHeight = $(this).height();    //当前可视的页面高度
        if(scrollTop + clientHeight == scrollHeight){
            //距离顶部+当前高度 >=文档总高度 即代表滑动到底部 注：-50 上拉加载更灵敏
            //加载数据
            if(that.ismore == true){
                that.getAllData();
            }
        }
    });
  },
  methods: {
    cardadd(){
      this.$router.push({name:'cardadd',query:{backtype:this.cardbacktype}});
    },
    getAllData(){
      let params = {
        uid: this.loginData.id,
        sid: this.loginData.sid,
        page_index: this.page,
        page_size: 10
      };
      UserBankCardList(params).then(res => {
        if (res.data.code === 0) {
          if(res.data.data.length>0){
            var getdata = res.data.data;
            if(getdata.length>0){
              for (var i = 0; i < getdata.length; i++) {
                this.cardList.push(getdata[i]);
              }
              if (getdata.length < 10) {
                  this.ismore = false;
              } else {
                  this.page++;
                  this.ismore = true;
              }
            }else{
                this.ismore = false;
            }
          }

        } else {
          this.$message.error(res.data.msg);
        }
      });
    }
  },
  beforeDestroy () {
    document.querySelector('html').removeAttribute('style')
  }
};
</script>

<style scoped>
.main_content {
  width: 100%;
  /* height: 100%; */
  background: #f4f4f4;
}
.card_all{
  display: flex;
  justify-content: space-between;
  padding: 13px;
}
.card_all label{
  color: #000;
  font-size: 15px;
}
.card_all span{
  color: #999;
  font-size: 13px;
}
.card_add{
  width: 18px;
  height: 18px;
}
.card_box {
  padding-top: 4px;
}
.card_box_item {
  background: url('../../assets/images/mine/card_bg.png');
  background-repeat: no-repeat;
  background-size: 100%;
  width: 351px;
  height: 120px;
  margin: 10px auto;
  position: relative;
}
.card_logo{
  position: absolute;
  top: 29px;
  left: 16px;
}
.card_logo img{
  width: 34px;
  height: 22px;
}
.item_content{
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
}

.item_content_title{
  color: #ffffff;
  font-size: 18px;
  font-family: 'PingFang-SC-Medium';
  margin-bottom: 16px;
  margin-left: 60px;
}
.item_content_num{
  font-family: 'PingFang-SC-Bold';
  font-size: 20px;
  color: #ffffff;
  margin-left: 20px;
  letter-spacing: 3px;
}
.item_content_num span{
  font-family: 'PingFang-SC-Medium';
  font-size: 17px;
  font-weight: normal;
  color: #ffffff;
}


/* 添加新地址 */
.card_nothing{
  margin-top: 110px;
}
.tips{
  width: 154px;
  margin: 0 auto;
}
.tips img{
  width: 100%;
}
.tips p{
  color: #666666;
  font-family: 'PingFang-SC-Medium';
  font-size: 13px;
}
.tips_text{
  font-size: 12px;
  text-align: left;
  color: #666666;
  width: 314px;
  margin: 0 auto;
  margin-top: 10px;
}
</style>



