<template>
  <div>
    <Index-header></Index-header>
    <div id="tab">
      <div class="tab-content">
        <div class="tab-content-item">
          <!-- <div class="tab-content-item-div"> -->
          <div class="tab-content-item-div" v-for="(item,index) in allProjects" :key="index">
            <div class="tab-content-item-left">
              <label>{{item.content}}</label>
              <span>{{item.create_time}}</span>
            </div>
            <div class="tab-content-item-right">
              <label>{{item.log_type}}</label>
              <span>{{item.change_money/100}}</span>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div>
      <div class="nodata" v-if="ismore">上拉加载更多</div>
      <div class="nodata" v-else>无更多数据</div>
    </div>
  </div>
</template>

<script>
import Header from "../common/Header";
import axios from "axios";
import Vue from "vue";
import commonURL from "../../assets/js/ajax";
import { UserWalletList } from "@/api/public.js";
import $ from "../../assets/js/jquery-3.3.1.min";
import wx from "weixin-js-sdk";
import moment from "moment";

export default {
  name: "menulist",
  data() {
    return {
      loginData: {}, //登陆的数据
      allProjects: [],
      page: 1,
      ismore: false
    };
  },
  components: {
    "Index-header": Header
  },
  beforeDestroy () {
    document.querySelector('html').removeAttribute('style')
  },
  created() {
    this.$store.state.title = "账单明细";
    this.$store.state.linkName = "myAccount";
  },
  mounted() {
    document.querySelector('html').setAttribute('style', 'background-color:#F5F5F5')
    this.loginData = JSON.parse(localStorage.getItem("loginData"));
    this.getAllData();
    var that = this;
    $(window).scroll(function() {
      var scrollTop = $(this).scrollTop(); //滚动条距离顶部的高度
      var scrollHeight = $(document).height(); //当前页面的总高度
      var clientHeight = $(this).height(); //当前可视的页面高度
      if (scrollTop + clientHeight == scrollHeight) {
        //距离顶部+当前高度 >=文档总高度 即代表滑动到底部 注：-50 上拉加载更灵敏
        //加载数据
        if (that.ismore == true) {
          that.getAllData();
        }
      }
    });
  },
  methods: {
    getAllData() {
      let params = {
        mobile: '',
        uid: this.loginData.id,
        sid: this.loginData.sid,
        page_index: this.page,
        page_size: 10
      };
      UserWalletList(params).then(res => {
        if (res.data.code === 0) {
          var getdata = res.data.data.list;
          if(getdata.length>0){
              for (var i = 0; i < getdata.length; i++) {
                if (getdata[i].log_type == 'deposit') {
                  getdata[i].log_type = "收入";
                }else if (getdata[i].log_type == 'withdraw') {
                  getdata[i].log_type = "支出";
                }else if (getdata[i].log_type == 'freeze') {
                  getdata[i].log_type = "冻结资金";
                }else if (getdata[i].log_type == 'unfreeze') {
                  getdata[i].log_type = "解冻资金";
                }else if (getdata[i].log_type == 'unfreeze_back') {
                  getdata[i].log_type = "冻结资金回退到余额账户";
                }
                var newDate = new Date();
                newDate.setTime(getdata[i].create_time * 1000);
                getdata[i].create_time = this.$moment(newDate).format(
                "YYYY-MM-DD HH:mm:ss"
                );
                this.allProjects.push(getdata[i]);
            }
            if (getdata.length < 10) {
                this.ismore = false;
            } else {
                this.page++;
                this.ismore = true;
            }
          }else{
              this.ismore = false;
          }

        } else {
          this.$message.error(res.data.msg);
        }
      });
    },
  }
};
</script>

<style scoped >
.tab-header {
  height: 40px;
  background: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
}
.tab-bar {
  display: flex;
  justify-content: space-around;
  width: 100%;
  height: 40px;
  color: #999;
}
.tab-bar li {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 70px;
  font-size: 13px;
}
.tab-bar-active {
  border-bottom: 1px #4076FB solid;
  color: #000;
}
.tab-content{
  background: #fff;
  margin-top: 5px;
}
.tab-content-item {
  margin: 6px auto;
  width: 93.6%;
}
.tab-content-item-div {
  display: flex;
  justify-content: space-between;
  height: 60px;
  padding: 0px 12px;
  margin-bottom: 6px;
  color: #333333;
  font-family: "PingFangSC-Regular";
  border-bottom: 1px solid #C8C8C8;
}
.tab-content-item :last-child{
  border-bottom: none;
}
.tab-content-item-left {
  display: flex;
  flex-direction: column;
  height: 60px;
  font-size: 14px;
  text-align: left;
}
.tab-content-item-left label{
  height: 30px;
  line-height: 35px;
}
.tab-content-item-left span{
  height: 30px;
  line-height: 25px;
  color: #A2A2A2;
  font-size: 13px;
}
.tab-content-item-right {
  display: flex;
  flex-direction: column;
  height: 60px;
  text-align: right;
  font-size: 16px;
  color: #000;
}
.tab-content-item-right label{
  height: 30px;
  line-height: 35px;
  color: #1DBF3F;
}
.tab-content-item-right span{
  height: 30px;
  line-height: 25px;
}
a {
  color: #333;
}
</style>



