<template>
  <div class="main_content">
    <el-container>
      <div class="login_bg">
        <div class="login_bg_img">
          <img src="../../assets/images/login_bg.png">
        </div>
        <div class="login_bg_title">登录</div>
        <div class="login_ul">
          <div class="login_info">
            <label>手机号</label>
            <input type="number" v-model="mobile" placeholder="请输入手机号">
          </div>
          <div class="login_info">
            <label>验证码</label>
            <div class="login_code">
              <input id="verify" class="loan_inut" placeholder="请输入验证码">
              <label id="getVerify">
                <span>
                  <a @click="makeIdentifyCode">获取验证码 </a><i></i>
                </span>
              </label>
            </div>
          </div>
          <p class="Ehint"></p>
          <div class="login_btn">
            <button @click="login">登录</button>
          </div>
          <div class="register_btn">
            <router-link :to="{name:'register'}"><button>注册</button></router-link>
          </div>
        </div>
      </div>
      <div class="mask" @click="close"></div>
      <div id="logout" class="dialog">
        <div class="dialog-content"></div>
        <div class="dialog-btn clearfix">
          <a class="dialog_sure2" @click="close">确认</a>
        </div>
      </div>
    </el-container>
  </div>
</template>

<script>
import axios from "axios";
import Vue from "vue";
import commonURL from "../../assets/js/ajax";
import { get_code , log_in } from "@/api/public.js";
import $ from "../../assets/js/jquery-3.3.1.min";
import wx from "weixin-js-sdk";
import moment from "moment";

export default {
  name: "login",
  data() {
    return {
      mobile: "",
      password: "",
      yzcode: '',
      InterValObj: '',  //timer变量，控制时间
      curCount: 60,  //当前剩余秒数
      haveTap: 0, // 屏蔽重复点击
      codeType: 5, //  1：注册，5：登录
    };
  },
  components: {},
  created() {},
  mounted() {},
  methods: {
    close() {
      $(".mask").hide();
      $("#logout").hide();
    },
    makeIdentifyCode(){
      const that = this;
      if (this.haveTap === 0) {
        var re = /^\d{11}$/;
        if(re.test(this.mobile)){
          that.haveTap = 1;
          let params = {
            accepter: that.mobile,
            code_type: that.codeType
          };
          $(".Ehint").hide();
          $("#getVerify a").hide();
          $("#getVerify i").text("获取验证码");
          //获取验证码
          get_code(params).then(res => {
            // console.log(res);
            that.haveTap = 0;
            if (res.data.code === 0) {
              this.curCount = 60;
              $("#getVerify i").text(this.curCount + "s");
              that.InterValObj = window.setInterval(function(){
                if (that.curCount == 1) {
                  window.clearInterval(that.InterValObj);
                  $("#getVerify a").show();
                  $("#getVerify a").text("重新发送");
                  $("#getVerify i").empty();
                } else {
                  that.curCount = Number(that.curCount)-1;
                  $("#getVerify i").text(that.curCount + "s");
                }
              }, 1000);
              that.$alert(res.data.msg, '提示', {
                confirmButtonText: '确定',
                callback: action => {
                }
              });
            }else{
              $("#getVerify a").show().text("重新发送");
              $("#getVerify i").empty();
              $(".Ehint").text("获取验证码失败，请重试！").show();
              setTimeout(function(){
                  $(".Ehint").hide();
              },3000);
              that.$alert(res.data.msg, '提示', {
                confirmButtonText: '确定',
                callback: action => {
                }
              });
            }
          });
        }else{
          $(".Ehint").text("提示：请输入正确的手机号码！").show();
          setTimeout(function(){
              $(".Ehint").hide();
          },3000);
        }
      } else {
        console.log("屏蔽重复点击");
        that.$message.error("点击频率过快");
      }
    },
    login() {
      const that = this;
      if (this.haveTap === 0) {
        if (this.mobile == "" ||  $("#verify").val() == "") {
          that.$message.error("请输入手机号和密码！");
        } else {
          var Regx = /^\d{11}$/;
          if(!Regx.test(this.mobile)){
            that.$message.error("请输入正确的手机号！");
            return false;
          }

          that.haveTap = 1;
          let params = {
            mobile: that.mobile,
            code: $("#verify").val()
          };
          log_in(params).then(res => {
            console.log(res);
            that.haveTap = 0;
            if (res.data.code === 0) {
                that.$cookies.set("status", "logined", '7d'); // 设置cookie中存放的生命周期
                that.$message.success("登陆成功");
                that.$router.push({name:'mine'})
                localStorage.setItem('loginData',JSON.stringify(res.data.data));
                that.loginData = JSON.parse(localStorage.getItem("loginData"));
            } else {
              that.$message.error(res.data.msg);
            }
          });
        }
      } else {
        console.log("屏蔽重复点击");
        that.$message.error("点击频率过快");
      }
    }
  },
  destroyed(){
    if(this.InterValObj) { //如果定时器在运行则关闭
      clearInterval(this.InterValObj);
    }
  }
};
</script>

<style scoped>
.main_content {
  width: 100%;
  height: 100%;
  background: #fff;
}
.login_bg{
  width: 100%;
  display: flex;
  flex-direction: column;
}
.login_bg_img{
  display: flex;
  width: 100%;
  justify-content: flex-end;
}
.login_bg_img img{
  display: block;
  width: 117px;
  height: 117px;
}
.login_bg_title{
  text-align: left;
  padding-left: 32px;
  margin-bottom: 20px;
  font-size: 32px;
  color: #000;
}
.login_ul {
  width: 100%;
  padding: 0px 32px;
}
.login_info {
  width: 100%;
  color: #333;
  font-size: 17px;
  border-bottom: 1px solid #F5F5F5;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
}
.login_info label {
  padding-top: 20px;
  padding-bottom: 5px;
  text-align: left;
  color: #1A1A1A;
}
.login_info input {
  width: 100%;
  border: none;
  padding: 10px 0px;
  display: flex;
  align-items: center;
  font-size: 14px;
}
.login_code{
  display: flex;
  justify-content: space-between;
}
.lastinfo{
  padding: 25px 0px;
  border-bottom: none !important;
}
.login_info p {
  width: 100%;
  text-align: right;
  font-size: 12px;
}
.login_info p a{
  color: red;
}
.login_btn {
  margin-top: 50px;
  margin-bottom: 20px;
  text-align: center;
}
.login_btn button {
  width: 100%;
  border: none;
  padding: 10px;
  border-radius: 4px;
  color: #fff;
  font-size: 1rem;
  background: #383838;
}
#verify{
  width: 70%;
}
#getVerify{
  height: 4px;
  line-height: 4px;
  padding: 10px 0px;
  margin-top: 5px;
}
input::-webkit-input-placeholder{
  color: #999999;
  font-size: 14px;
}
p{
  width: 50%;
  margin: auto;
  text-align: center;
  padding: 25px 0px;
  font-size: 12px;
}
.register_btn{
  float: left;
  width: 100%;
  text-align: center;
}
.register_btn button {
  width: 100%;
  border: 1px solid #383838;
  padding: 10px;
  border-radius: 4px;
  color: #383838;
  font-size: 1rem;
  background: #fff;
}
</style>



