<template>
  <div class="main_content">
    <Index-header></Index-header>
    <el-container>
      <div class="rz_content">
        <div class="login_ul">
          <div class="login_info">
            <label><span>姓</span><span>名</span></label>
            <span class="login_info_span" v-if="isWait || isSuccess">{{name}}</span>
            <input type="text" v-model="name" placeholder="请输入姓名" v-else>
          </div>
          <div class="login_info">
            <label><span>身</span><span>份</span><span>证</span><span>号</span></label>
            <span class="login_info_span" v-if="isWait || isSuccess">{{id_no}}</span>
            <input type="text" v-model="id_no" placeholder="请输入身份证号" v-else>
          </div>
        </div>
        <img class="status_img" src="../../assets/images/mine/isWait.png" v-if="isWait">
        <img class="status_img" src="../../assets/images/mine/isSuccess.png" v-if="isSuccess">
        <img class="status_img" src="../../assets/images/mine/isFaild.png" v-if="isFaild">
        <div class="login_ul">
          <div class="pic_content">
            <label v-if="isWait || isSuccess">身份证照片</label>
            <label v-else>请拍摄并上传你的身份证照片</label>
            <div class="pic_all" v-if="isWait || isSuccess">
              <div class="pic_left">
                <div class="pic_top">
                  <img :src="id_front_img">
                </div>
                <span>正面</span>
              </div>
              <div class="pic_left">
                <div class="pic_top">
                    <img :src="id_back_img">
                </div>
                <span>反面</span>
              </div>
            </div>
            <div class="pic_all" v-else>
              <div class="pic_left">
                <div class="pic_top">
                  <el-upload width="100%"
                    with-credentials
                    class="img_submit"
                    accept=".png, .jpeg, .jpg"
                    name="image"
                    :show-file-list="false"
                    :action="pictureUpload"
                    :data="{t:'id_card',uid:loginData.id,sid:loginData.sid,oss_type:''}"
                    :on-success="projectCoverUpload"
                  >
                    <img :src="id_front_img">
                  </el-upload>
                </div>
                <button>上传正面</button>
              </div>
              <div class="pic_left">
                <div class="pic_top">
                  <el-upload width="100%"
                    with-credentials
                    class="img_submit"
                    accept=".png, .jpeg, .jpg"
                    name="image"
                    :show-file-list="false"
                    :action="pictureUpload"
                    :data="{t:'id_card',uid:loginData.id,sid:loginData.sid,oss_type:''}"
                    :on-success="projectCoverUpload2"
                  >
                    <img :src="id_back_img">
                  </el-upload>
                </div>
                <button>上传反面</button>
              </div>
            </div>
          </div>
        </div>
        <div class="login_ul">
          <div class="pic_content">
            <label style="padding-bottom:10px">拍摄身份证要求：</label>
            <label class="con_tishi">大陆公民持有的本人有效二代身份证；</label>
            <label class="con_tishi">拍摄时确保身份证 <span>边框完整，字体清晰，亮度均匀。</span></label>
            <div class="pic_all">
              <div class="pic_left2">
                <div class="pic_top">
                  <img src="../../assets/images/mine/rz_1.png">
                </div>
                <label>标准</label>
              </div>
              <div class="pic_left2">
                <div class="pic_top">
                  <img src="../../assets/images/mine/rz_2.png">
                </div>
                <label>边框缺失</label>
              </div>
              <div class="pic_left2">
                <div class="pic_top">
                  <img src="../../assets/images/mine/rz_3.png">
                </div>
                <label>照片模糊</label>
              </div>
              <div class="pic_left2">
                <div class="pic_top">
                  <img src="../../assets/images/mine/rz_4.png">
                </div>
                <label>闪光强烈</label>
              </div>
            </div>
          </div>
        </div>
        <div class="login_btn" v-if="isWait || isSuccess">
        </div>
        <div class="login_btn" v-else>
          <button @click="login">身份认证</button>
        </div>
      </div>
    </el-container>
  </div>
</template>

<script>
import Header from "../common/Header";
import axios from "axios";
import Vue from "vue";
import commonURL from "../../assets/js/ajax";
import { picture_upload , UserIdCardInfo , createUserIdCard } from "@/api/public.js";
import $ from "../../assets/js/jquery-3.3.1.min";
import wx from "weixin-js-sdk";
import moment from "moment";

export default {
  name: "authentically",
  data() {
    return {
      name: "",
      id_no: "",
      verify: "",
      id_front_img: require("../../assets/images/mine/pic_zheng.png"),
      id_back_img: require("../../assets/images/mine/pic_fan.png"),
      haveTap: 0, // 屏蔽重复点击
      loginData: {}, //登陆的数据
      isWait:false,
      isSuccess:false,
      isFaild:false,
    };
  },
  components: {
    "Index-header": Header
  },
  created() {
    this.$store.state.title = "实名认证";
    this.$store.state.linkName = "mine";
    this.pictureUpload = picture_upload;
  },
  mounted() {
    document.querySelector('html').setAttribute('style', 'background-color:#F5F5F5')

    if(!!localStorage.getItem("loginData")){
      this.loginData = JSON.parse(localStorage.getItem("loginData"));
      this.getIfAuth();
    }

  },
  methods: {

    close() {
      $(".mask").hide();
      $("#logout").hide();
    },

    projectCoverUpload(res, file) {
      const that = this;
      if (res.code === 0) {
        that.id_front_img =  res.data.relative_path;
      } else if (res.code === 1111) {
        that.$message.error("请登录");
        this.$router.push({name:'login'});
      }
    },
    getIfAuth(){
      let params = {
        uid: this.loginData.id,
        sid: this.loginData.sid
      };
      UserIdCardInfo(params).then(res => {
        if (res.data.code === 0) {
          if(res.data.data){
            if(res.data.data.verify == 0){
              this.isWait = true;
            }else if(res.data.data.verify == 1){
              this.isSuccess = true;
            }else if(res.data.data.verify == -1){
              this.isFaild = true;
            }
            if(res.data.data.id_front_img){
              this.id_front_img = res.data.data.id_front_img;
            }
            if(res.data.data.id_back_img){
              this.id_back_img = res.data.data.id_back_img;
            }
            if(res.data.data.id_no){
              this.id_no = res.data.data.id_no;
            }
            if(res.data.data.name){
              this.name = res.data.data.name;
            }
          }
        } else {
          this.$message.error(res.data.msg);
        }
      });
    },

    projectCoverUpload2(res, file) {
      const that = this;
      if (res.code === 0) {
        that.id_back_img =  res.data.relative_path;
      } else if (res.code === 1111) {
        that.$message.error("请登录");
        this.$router.push({name:'login'});
      }
    },

    login() {
      if (this.name == "" || this.id_no == "") {
        this.$message.error("请输入完整信息");
      } else {
        var idcardReg = /^[1-9]\d{7}((0\d)|(1[0-2]))(([0|1|2]\d)|3[0-1])\d{3}$|^[1-9]\d{5}[1-9]\d{3}((0\d)|(1[0-2]))(([0|1|2]\d)|3[0-1])\d{3}([0-9]|X)$/;
        if(!idcardReg.test(this.id_no)){
          this.$message.error("请输入正确的身份证号！");
          return false;
        }else{
          const that = this;
          if (that.haveTap === 0) {
            that.haveTap = 1;
            let params = {
              name: that.name,
              id_no: that.id_no,
              id_front_img: that.id_front_img,
              id_back_img: that.id_back_img,
              uid: that.loginData.id,
              sid: that.loginData.sid
            };
            createUserIdCard(params).then(res => {
              that.haveTap = 0;
              if (res.data.code === 0) {
                that.$message.success('已成功提交认证!');
                this.getIfAuth();
              } else {
                that.$message.error(res.data.msg);
              }
            });
          }
        }
      }
    }
  },
  beforeDestroy () {
    document.querySelector('html').removeAttribute('style')
  }
};
</script>

<style scoped>
.main_content {
  width: 100%;
  height: 100%;
}
.rz_content{
  display: flex;
  flex-direction: column;
  width: 100%;
  position: relative;
}
.login_ul {
  display: flex;
  flex-direction: column;
  width: 100%;
  padding: 0px 10px;
  margin-bottom: 10px;
  background: #fff;
}
.login_info {
  display: flex;
  width: 100%;
  padding: 10px 0px;
  color: #333;
  font-size: 13px;
  box-sizing: border-box;
}
.login_info label {
  display: flex;
  width: 60px;
  padding: 10px 0px;
  text-align: left;
  justify-content: space-between;
}
.login_info input {
  float: left;
  width: 70%;
  margin-left: 3%;
  border: none;
  padding: 10px 5px;
}
.login_info_span {
  float: left;
  width: 70%;
  margin-left: 3%;
  border: none;
  padding: 10px 5px;
  text-align: left;
}
.pic_content{
  display: flex;
  flex-direction: column;
  width: 100%;
  color: #333;
  font-size: 15px;
  text-align: left;
  padding: 10px 0px;
}
.pic_all{
  display: flex;
  justify-content: center;
  width: 100%;
  padding: 10px 0px;
}
.pic_left{
  display: flex;
  flex-direction: column;
  width: 50%;
  font-size: 15px;
  padding: 0px 15px;
}
.pic_top{
  background: #F4F8FE;
}
.pic_top img{
  width: 80%;
  height: 79px;
  margin: 10px auto;
  display: block;
}
.pic_left button{
  background: #4280F2;
  height: 30px;
  line-height: 30px;
  color: #fff;
  font-size: 12px;
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;
  text-align: center;
  border: none;
}
.pic_left span{
  height: 30px;
  line-height: 30px;
  font-size: 12px;
  text-align: center;
}
.con_tishi{
  color: #919191;
  font-size: 13px;
  text-align: left;
}
.con_tishi span{
  color: #E73146;
}
.pic_left2{
  display: flex;
  flex-direction: column;
  width: 25%;
  font-size: 13px;
  padding: 0px 5px;
}
.pic_left2 .pic_top img{
  width: 100%;
  height: auto;
  margin: auto;
}
.pic_left2 label{
  margin-top: 5px;
  text-align: center;
  color: #919191;
}
.status_img{
  width: 60px;
  height: 62px;
  position: absolute;
  top: 20px;
  right: 20px;
}
p {
  float: left;
  width: 50%;
  margin-left: 25%;
  padding: 25px 0px;
  font-size: 13px;
}
.login_btn {
  width: 96%;
  margin: 10px auto;
  text-align: center;
}
.login_btn button {
  width: 100%;
  border: none;
  padding: 10px;
  border-radius: 4px;
  color: #fff;
  font-size: 1rem;
  background: #4076FB;
}
</style>



