import Vue from 'vue'
import axios from "axios";

// const URL = "/dev/api";
const URL = "http://api.guoxuekong.com/index.php/base";
const getAllParams = {
  app_type: process.env.NODE_ENV === 'h5',// 【公共】应用类型支持ios、web、android, undefined, text,选填
  app_version: "1.0.0",// 【公共】应用版本类似1.0.0, , text,选填
  notify_id: 1231231,// 【公共】请求流水id唯一, , text,选填
  app_request_time: 100000,// 【公共】请求时间戳 秒级, , text,选填
  lang: "zh-cn",// 【公共】支持zh-cn、en, zh-cn, text,选填
  client_id: "by04esfH0glASt",// 【公共】应用id，分配得到每个接口必传, , text,选填
  service_version: "100",// 【公共】请求服务的版本，默认100, 100, text,选填
  sign: "",
  uid: 0,
  sid: "",
  buss_data: "",
  country_no: "86"
}

//  用户
//    验证码-创建并发送（短信）
// POST
const get_code_params = {
  ...getAllParams,
  service_type: "by_SecurityCode_createAndSend",// 【公共】请求服务类型, by_UserLoginSession_loginByMobileCode, text,
  accepter: "",// 手机号, , text,选填
  code_type: "1",// 注册: 1更新密码:2绑定手机号,之前未绑定过:3更换手机号:4;登录:5;找回密码:6;验证是否本人操作:7;, 5, text,country_no:"",// 国家区号不需要加号, 86, text,选填
  code_length: 6
}

export const get_code = (data) => {
  return axios({
    method: "post",
    url: URL,
    data: { ...get_code_params,...data }
  })
}

//    注册 （手机号 + 短信验证码）
// POST
const sign_up_params = {
  ...getAllParams,
  service_type: "by_UserLoginSession_registerByMobileCode",// 【公共】请求服务类型, by_UserLoginSession_loginByMobileCode, text,选填
  mobile: "",// 手机号, , text,选填
  code: "hebidu",// 短信验证码, hebidu, text,选填
  device_token: "",// 设备标识, , text,选填
  device_type: "",// 设备类型(获取浏览器的类型), , text,选填
}

export const sign_up = (data) => {
  return axios({
    method: "post",
    url: URL,
    data: { ...sign_up_params,...data }
  })
}

