<template>
  <div class="main_content">
    <Index-header></Index-header>
    <el-container>
      <div class="card">
        <div class="card_box">
          <div class="card_box_item">
            <div class="card_logo">
              <img src="../../assets/images/mine/card_sign.png" />
            </div>
            <div class="item_content">
              <div class="item_content_title">{{name}}</div>
              <div class="item_content_num">{{card}}</div>
            </div>
          </div>
        </div>
      </div>
    </el-container>
  </div>
</template>

<script>
import Header from "../common/Header2";
import axios from "axios";
import Vue from "vue";
import commonURL from "../../assets/js/ajax";
import { delUserBankCard } from "@/api/public.js";
import $ from "../../assets/js/jquery-3.3.1.min";
import wx from "weixin-js-sdk";
import moment from "moment";

export default {
  name: "carddetail",
  data() {
    return {
      loginData: {}, //登陆的数据
      name: "",
      cardcom: "",
      card: "",
      backtype:'',
      isedit: false,
      haveTap: 0, // 屏蔽重复点击
      id: ''
    };
  },
  components: {
    "Index-header": Header
  },
  created() {
    if(this.$route.query.backtype){
      this.backtype = this.$route.query.backtype;
      this.cardbacktype = this.backtype;
    }
    if(this.$route.query.carddata){
      this.name = this.$route.query.carddata.opening_bank;
      this.card = this.$route.query.carddata.card_no;
      this.id = this.$route.query.carddata.id;
    }
    this.$store.state.title = "详情";
    this.$store.state.linkName = "cardlist";
    this.$store.state.linkQuery = {backtype:this.backtype};
  },
  mounted() {
    if(!!localStorage.getItem("loginData")){
      this.loginData = JSON.parse(localStorage.getItem("loginData"));
    }
    document.querySelector('html').setAttribute('style', 'background-color:#F5F5F5')
  },
  methods: {
    sureDelete() {
      const that = this;
      if (this.haveTap === 0) {
        that.haveTap = 1;
        let params = {
          id: this.id,
          uid: this.loginData.id,
          sid: this.loginData.sid
        };
        delUserBankCard(params).then(res => {
          console.log(res);
          that.haveTap = 0;
          if (res.data.code === 0) {
            that.$router.push({ name: "cardlist" ,query:{backtype:this.backtype}});
          } else {
            that.$message.error(res.data.msg);
          }
        });
      } else {
        console.log("屏蔽重复点击");
        that.$message.error("点击频率过快");
      }
    },
  }
};
</script>

<style scoped>
.main_content {
  width: 100%;
  height: 100%;
}
.card{
  width: 100%;
}
.card_box {
  padding-top: 4px;
}
.card_box_item {
  background: url('../../assets/images/mine/card_bg.png');
  background-repeat: no-repeat;
  background-size: 100%;
  width: 351px;
  height: 120px;
  margin: 10px auto;
  position: relative;
}
.card_logo{
  position: absolute;
  top: 29px;
  left: 16px;
}
.card_logo img{
  width: 34px;
  height: 22px;
}
.item_content{
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
}

.item_content_title{
  color: #ffffff;
  font-size: 18px;
  font-family: 'PingFang-SC-Medium';
  margin-bottom: 16px;
  margin-left: 60px;
}
.item_content_num{
  font-family: 'PingFang-SC-Bold';
  font-size: 20px;
  color: #ffffff;
  margin-left: 20px;
  letter-spacing: 3px;
}
.item_content_num span{
  font-family: 'PingFang-SC-Medium';
  font-size: 17px;
  font-weight: normal;
  color: #ffffff;
}
.bottom_btn{
  width: 100%;
  margin: 20px auto;
}
.login_btn {
  width: 96%;
  margin: auto;
  text-align: center;
}
.login_btn button {
  width: 100%;
  border: none;
  padding: 10px;
  border-radius: 4px;
  color: #fff;
  font-size: 1rem;
  background: #383838;
}
</style>



