<template>
  <div id="app">
    <div class="main">
      <router-view v-if="isRouterAlive"/>
    </div>
  </div>
</template>

<script>
export default {
  name: 'App',
  provide () {
    return {
      reload: this.reload
    }
  },
  data () {
    return {
      isRouterAlive: true
    }
  },
  components:{

  },
  methods: {
    reload () {
      this.isRouterAlive = false
      this.$nextTick(function () {
        this.isRouterAlive = true
      })
    }
  }
}
</script>

<style>
#app {
  font-family: 'MicrosoftYaHei', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}
.main{
  width: 100%;
  background-color: #F5F5F5;
  height: 100%;
  overflow: auto;
  z-index: -100;
}
.ad_header {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  z-index: 9;
  /* background-color: #e4e7ed; */
}
</style>



