<template>
  <div class="footer">
    <div
      v-for="(item,index) of items"
      :key="index"
      :class="[item.cls,{on:index === idx}]"
      @click="bottomNav(item)"
    >
      <img :src="index===idx?item.iconSelect:item.icon" :class="index==0?'nosame':''">
      <p :class="['colorChange',{on:index===idx}]">{{item.name}}</p>
    </div>
  </div>
</template>
<script>
import commonURL from "../../assets/js/ajax";
export default {
  props: ["idx"],
  data() {
    return {
      items: [
        {
          cls: "home",
          name: "首页",
          push: "/home",
          icon: require("../../assets/images/home_noselected.png"),
          iconSelect: require("../../assets/images/home_selected.png")
        },
        {
          cls: "mine",
          name: "我的",
          push: "/mine",
          icon: require("../../assets/images/mine_noselected.png"),
          iconSelect: require("../../assets/images/mine_selected.png")
        }
      ]
    };
  },
  mounted() {
    // if (!!localStorage.getItem("loginData")) {
    //   this.loginData = JSON.parse(localStorage.getItem("loginData"));
    // }
  },
  methods: {
    bottomNav(item) {
			if(!!localStorage.getItem("loginData")){
        this.loginData = JSON.parse(localStorage.getItem("loginData"));
        this.$router.push(item.push);
      }else{
        this.$router.push({name:'login'});
      }
    }
  }
};
</script>
<style>
.footer {
  display: flex;
  position: fixed;
  left: 0;
  bottom: 0;
  box-sizing: border-box;
  background: #fff;
  width: 100%;
  padding: 6px 20px;
  border-top: 1px solid #f8f8f8;
  z-index: 99999;
}
.footer div {
  flex: 1;
  font-size: 0.6rem;
}
.footer img {
  width: 12%;
}
.nosame {
  width: 14% !important;
}
.footer div p {
  color: #999;
}
.on {
  color: #333 !important;
}
</style>



