<template>
  <div class="main_content">
    <Index-header></Index-header>
    <el-container>
      <div class="my_account">
        <div class="login_ul">
          <div class="login_info">
            <label><span>姓</span><span>名</span></label>
            <input type="text" v-model="name" placeholder="请输入持卡人姓名" >
          </div>
          <div class="login_info">
            <label><span>银</span><span>行</span><span>卡</span><span>号</span></label>
            <input type="number" v-model="card" placeholder="请输入银行卡号" >
          </div>
          <div class="login_info">
            <label><span>银</span><span>行</span><span>名</span><span>称</span></label>
            <input type="text" v-model="cardcom" placeholder="请输入银行名称" >
          </div>
          <div class="login_info" style="border-bottom:none">
            <label><span>开</span><span>户</span><span>行</span></label>
            <input type="text" v-model="cardname" placeholder="请输入开户行" >
          </div>
        </div>
        <div class="bottom_btn">
          <div class="login_btn">
            <button @click="sure" >确认</button>
          </div>
        </div>
      </div>
      <div class="mask" @click="close"></div>
      <div id="logout" class="dialog">
        <div class="dialog-content"></div>
        <div class="dialog-btn clearfix">
          <a class="dialog_sure2" @click="close">确认</a>
        </div>
      </div>
    </el-container>
  </div>
</template>

<script>
import Header from "../common/Header";
import axios from "axios";
import Vue from "vue";
import commonURL from "../../assets/js/ajax";
import { addUserBankCard } from "@/api/public.js";
import $ from "../../assets/js/jquery-3.3.1.min";
import wx from "weixin-js-sdk";
import moment from "moment";

export default {
  name: "cardadd",
  data() {
    return {
      loginData: {}, //登陆的数据
      name: "",
      cardcom: "",
      cardname: "",
      card: "",
      backtype:'',
      isedit: false,
      haveTap: 0, // 屏蔽重复点击
      id: ''
    };
  },
  components: {
    "Index-header": Header
  },
  created() {
    if(this.$route.query.backtype){
      this.backtype = this.$route.query.backtype;
      this.cardbacktype = this.backtype;
    }
    this.$store.state.title = "添加银行卡";
    this.$store.state.linkName = "cardlist";
    this.$store.state.linkQuery = {backtype:this.backtype};
  },
  mounted() {
    if(!!localStorage.getItem("loginData")){
      this.loginData = JSON.parse(localStorage.getItem("loginData"));
    }
    document.querySelector('html').setAttribute('style', 'background-color:#F5F5F5')
  },
  methods: {
    close() {
      $(".mask").hide();
      $("#logout").hide();
    },
    sure() {
      if (this.name == "" || this.cardcom == "" || this.card == "") {
        this.$message.error("请输入完整内容！");
      } else {
        const that = this;
        if (this.haveTap === 0) {
          that.haveTap = 1;
          let params = {
            name: this.name,
            card_no: this.card,
            bank_name: this.cardcom,
            branch_name: this.cardname,
            uid: this.loginData.id,
            sid: this.loginData.sid
          };
          addUserBankCard(params).then(res => {
            console.log(res);
            that.haveTap = 0;
            if (res.data.code === 0) {
              that.$message.success(res.data.msg);
              that.$router.push({ name: "cardlist" ,query:{backtype:this.backtype}});
            } else {
              that.$message.error(res.data.msg);
            }
          });
        } else {
          console.log("屏蔽重复点击");
          that.$message.error("点击频率过快");
        }
      }
    },
  }
};
</script>

<style scoped>
.main_content {
  width: 100%;
  height: 100%;
}
h4 {
  position: relative;
  text-align: center;
  height: 44px !important;
  line-height: 44px;
  padding: 0 10px;
  color: #333;
  background: #fff;
  border-bottom: 1px solid #e3e3e3;
}
h4 a {
  position: absolute;
  right: 10px;
  color: #333;
  font-size: 12px;
  font-weight: 400;
}
.my_account{
  display: flex;
  flex-direction: column;
  width: 100%;
  margin-top: 5px;
}
.login_ul {
  width: 100%;
  background: #fff;
}
.login_info {
  color: #333;
  font-size: 13px;
  border-bottom: 1px solid #e3e3e3;
  box-sizing: border-box;
  display: flex;
  height: 45px;
  line-height: 45px;
  margin: 0px 10px;
}
.login_info label {
  width: 60px;
  display: flex;
  justify-content: space-between;
}
.login_info input {
  width: 70%;
  margin-left: 5%;
  border: none;
  padding: 0px 5px;
  display: flex;
  align-items: center;
}
.lastinfo{
  padding: 25px 0px;
  border-bottom: none !important;
}
.login_info span{
  width: 20%;
  float: left;
  text-align: left;
  cursor: pointer;
}
.login_info span a{
  color: red;
}
.login_info p {
  width: 80%;
  text-align: right;
  font-size: 12px;
}
.bottom_btn{
  width: 100%;
  margin: 20px auto;
}
.login_btn {
  width: 96%;
  margin: auto;
  text-align: center;
}
.login_btn button {
  width: 100%;
  border: none;
  padding: 10px;
  border-radius: 4px;
  color: #fff;
  font-size: 1rem;
  background: #383838;
}
</style>



