<template>
  <div class="main_content">
    <Index-header></Index-header>
    <div class="share_bg">
      <img class="share_bg1" src="../../assets/images/mine/update_bg.png" />
    </div>
    <div class="line"></div>
    <div class="info">
      <label>
        <img class="" src="../../assets/images/mine/account_logo.png" />
        账号
      </label>
      <span>{{loginData.mobile}}</span>
    </div>
    <div class="select_level">
      <div>
        <img @click="changePay($event,1)" src="../../assets/images/mine/update_v1y.png" v-if="nowLevel==0"/>
        <img src="../../assets/images/mine/update_v1.png" v-else />
      </div>
      <div>
        <img @click="changePay($event,2)" src="../../assets/images/mine/update_v2y.png" v-if="nowLevel<2"/>
        <img src="../../assets/images/mine/update_v2.png" v-else />
      </div>
      <div>
        <img @click="changePay($event,3)" src="../../assets/images/mine/update_v3y.png" v-if="nowLevel<3"/>
        <img src="../../assets/images/mine/update_v3.png" v-else />
      </div>
      <div>
        <img @click="changePay($event,4)" src="../../assets/images/mine/update_v4y.png" v-if="nowLevel<4"/>
        <img src="../../assets/images/mine/update_v4.png" v-else />
      </div>
      <div>
        <img @click="changePay($event,5)" src="../../assets/images/mine/update_v5y.png" v-if="nowLevel<5"/>
        <img src="../../assets/images/mine/update_v5.png" v-else />
      </div>
      <div>
        <img @click="changePay($event,6)" src="../../assets/images/mine/update_v6y.png" v-if="nowLevel<6"/>
        <img src="../../assets/images/mine/update_v6.png" v-else />
      </div>
      <div>
        <img @click="changePay($event,7)" src="../../assets/images/mine/update_v7y.png" v-if="nowLevel<7"/>
        <img src="../../assets/images/mine/update_v7.png" v-else />
      </div>
      <div>
        <img @click="changePay($event,8)" src="../../assets/images/mine/update_v8y.png" v-if="nowLevel<8"/>
        <img src="../../assets/images/mine/update_v8.png" v-else />
      </div>
      <div>
        <img @click="changePay($event,9)" src="../../assets/images/mine/update_v9y.png" v-if="nowLevel<9"/>
        <img src="../../assets/images/mine/update_v9.png" v-else />
      </div>
    </div>
    <div class="payfor">
      需支付：<span>{{payMoney}}</span> 元
    </div>
    <div class="login_btn">
      <button @click="update">立即升级</button>
    </div>
    <div class="mask" @click="close"></div>
    <div id="logout" class="dialog">
      <div class="dialog-content"></div>
      <div class="dialog-btn clearfix">
        <a class="dialog_sure2" @click="close">确认</a>
      </div>
    </div>
  </div>
</template>

<script>
import Header from "../common/Header4";
import axios from "axios";
import Vue from "vue";
import commonURL from "../../assets/js/ajax";
import { Base64 } from 'js-base64';
import { upgradeToVip , updateProfitGraph , refreshUserInfo } from "@/api/public.js";
import $ from "../../assets/js/jquery-3.3.1.min";
import wx from "weixin-js-sdk";
import moment from "moment";

export default {
  name: "updateLevel",
  data() {
    return {
      loginData: {}, //登陆的数据
      payMoney:"",
      haveTap: 0, // 屏蔽重复点击
      golevel: '',
      nowLevel:''
    };
  },
  components: {
    "Index-header": Header
  },
  inject: ['reload'],
  created() {
    this.$store.state.title = "发起升级";
    this.$store.state.linkName = "mine";
  },
  mounted() {

    // this.reload();
    if(!!localStorage.getItem("loginData")){
      this.loginData = JSON.parse(localStorage.getItem("loginData"));

      // let params = {
      //   uid: this.loginData.id,
      //   sid: this.loginData.sid
      // };
      // updateProfitGraph(params).then(resp => {
      //   if (resp.data.code === 0) {
      //     var olddata = JSON.parse(localStorage.getItem("loginData"));
      //     olddata.vip_level = resp.data.data.vip_level;
      //     localStorage.setItem('loginData',JSON.stringify(olddata));
      //     this.loginData = JSON.parse(localStorage.getItem("loginData"));
      //     console.log(this.loginData)
      //   } else {
      //     this.$message.error(resp.data.msg);
      //   }
      // });

      if(this.loginData.vip_level == '0'){
        this.payMoney = 499;
      }else{
        this.payMoney = 200;
      }
      this.nowLevel = Number(this.loginData.vip_level);
    }

  },
  methods: {
    close() {
      $(".mask").hide();
      $("#logout").hide();
    },
    changePay(e,level){
      if(this.loginData.vip_level == '0'){
        if(level == 1){
          this.golevel = level;
          this.payMoney = 499;
          $(e.currentTarget.parentElement).addClass('box').siblings().removeClass('box');
        }else{
          $(".mask").show();
          $("#logout").show();
          $(".dialog-content").text('需先充值V1，才可进行后续升级');
        }
      }else{
        this.golevel = level;
        var vip = Number(this.loginData.vip_level);
        this.payMoney = (level-vip)*200;
        $(e.currentTarget.parentElement).addClass('box').siblings().removeClass('box');
      }
    },
    update(){
      const that = this;
      if (this.haveTap === 0) {
        if(that.golevel == ''){
          this.$message.error('请先选择要升的等级');
          return;
        }
        that.haveTap = 1;
        let params = {
          uid: this.loginData.id,
          sid: this.loginData.sid,
          level: that.golevel
        };
        upgradeToVip(params).then(res => {
          that.haveTap = 0;
          if (res.data.code === 0) {
            var geturl = Base64.decode(res.data.data);
            window.location.href = geturl;
            //支付成功更新用户数据
            let params = {
              uid: this.loginData.id,
              sid: this.loginData.sid
            };
            updateProfitGraph(params).then(resp => {
              if (resp.data.code === 0) {
                var olddata = JSON.parse(localStorage.getItem("loginData"));
                olddata.vip_level = resp.data.data.vip_level;
                localStorage.setItem('loginData',JSON.stringify(olddata));
                this.loginData = JSON.parse(localStorage.getItem("loginData"));
                console.log(that.loginData)
              } else {
                this.$message.error(resp.data.msg);
              }
            });
            // that.reload();
            // that.query();
          } else {
            that.$message.error(res.data.msg);
          }
        });
      } else {
        console.log("屏蔽重复点击");
        that.$message.error("点击频率过快");
      }
    }
  }
};
</script>

<style scoped>
.main_content{
    height: 100%;
    position: relative;
    background: #fff;
}
.share_bg{
  width: 100%;
  position: relative;
}
.share_bg1{
  display: block;
  width: 100%;
}
.info {
  width: 100%;
  color: #333;
  font-size: 14px;
  display: flex;
  flex-direction: column;
  text-align: left;
  padding: 10px;
  background: #fff;
}
.info label{
  display: flex;
}

.info label img{
  width: 6px;
  height: 19px;
  margin-right: 5px;
}
.info span{
  font-size: 16px;
  margin-top: 10px;
  margin-left: 10px;
}
.line{
  display: flex;
  width: 100%;
  background: #e9e9e9;
  height: 8px;
}
.select_level{
  width: 100%;
  display: flex;
  justify-content: space-evenly;
  flex-wrap: wrap;
  padding: 10px 0px;
  background: #fff;

}
.select_level div{
  display: flex;
  width: 30%;
  height: 70px;
  margin-bottom: 5px;
}
.select_level img{
  width: 100%;
  display: block;
}
.payfor{
  width: 96%;
  margin: auto;
  text-align: center;
  background: #F1F2F4;
  font-size: 13px;
  padding: 10px;
  color: #D69C25;
}
.payfor span{
  font-size: 16px;
}
.login_btn {
  width: 96%;
  margin: auto;
  padding-bottom: 20px;
  text-align: center;
}
.login_btn button {
  width: 100%;
  border: none;
  padding: 10px;
  border-radius: 4px;
  color: #fff;
  font-size: 1rem;
  background: #383838;
}
.box {
  position: relative;
}
.box::before,
.box::after {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    border-color: transparent;
    border-style: solid;
}
.box::before {
    border-width: 12px;
    border-right-color: #d0a11c;
    border-top-color: #d0a11c;
}
.box::after {
    border-width: 12px;
    border-right-color: #d0a11c;
    border-top-color: #d0a11c;
}
</style>



